/* ==========================================================================
   6. Testimonial css start
   ========================================================================== */



.testimonials {
    background: #141414;
    text-align: center;

    .quotes {
        width: 40px;
        margin: auto;
        display: block;
        margin-bottom: 25px;
    }

    .owl-carousel {
        cursor: move;

        .t-content {
            margin: 0 25px;

            p {
                color: rgba(255, 255, 255, 0.5);
                font-size: 15px;
                line-height: 2.5;
                text-align: center;
                margin-bottom: 25px;

            }

            h3 {
                color: rgba(255, 255, 255, 0.97);
                font-size: 17px;
                font-weight: 500;
                text-align: right;
                margin-right: 15%;
            }

        }

        .owl-dots {
            margin-top: 25px;

            .owl-dot span {
                width: 10px;
                height: 10px;
                background: rgba(255, 255, 255, 0.97);
                margin: 10px;
                border-radius: 50%;
                transform: scale(0.8);
                transition: all 0.3s ease;

                &:hover {
                    background: rgba(255, 177, 0, 0.85);
                    transform: scale(1);
                }
            }

            .owl-dot.active span {
                background: rgba(255, 177, 0, 0.85);
                transform: scale(1);
            }
        }

    }




}



/* ==========================================================================
   6. Testimonial css end
   ========================================================================== */